<script>
    export default {
        data(){
            return{
                email : '',
                password : ''
            }
        },
        methods:{
            createLoginRequest(){
                let req = {
                    email : this.email,
                    password : this.password
                }
                this.$http.post('http://18.217.149.238:3000/login',req).then(res=>{
                    if(res.status === 200){
                        localStorage._token = res.body.token
                        localStorage.user = res.body.details._id
                        location.reload()
                    }
                })
            }
        },
        beforeMount(){
            if(localStorage._token){
                this.$router.push('/')
            }
        }
    }
</script>


<template>
    <div class="container">
    <form action="index.html" method="post">
        <h1>
        Sign in
        </h1>
        <div class="form-content">
            <input  name="email" placeholder="Enter your email" type="text" v-model="email" required/>
            <input id="password" name="password" placeholder="password" type="password" v-model="password"/><br />
        <div class="button"  @click.prevent="createLoginRequest()">
            Log in
        </div>
        <br />
        
        </div>
    </form>
    </div>
</template>


